import { unified } from "unified";
import rehypeDomParse from "rehype-dom-parse";
import rehypeDomStringify from "rehype-dom-stringify";

export default function parse(ast) {
    let story = [];
    let currentUnit = {};
    ast.forEach((unit) => {
        console.log(unit);
        // if(unit.)
        switch (unit.tagName) {
            case "p":
                break;
            case "blockquote":
                break;
        }
        story.push({
            content: unified()
                .use(rehypeDomParse)
                .use(rehypeDomStringify, { fragment: true })
                .parse(unit.children),
        });
    });
    return story;
}
